import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import UnityDescription from "../soft/unityDescription/UnityDescription";
import Service from "../soft/service/Service";
import Technology from "../soft/technology/Technology";
import PortfolioCompleted from "../soft/portfolioCompleted/PortfolioCompleted";
import PortfolioMedia from "./portfolioMedia/PortfolioMedia";
import Event from "../soft/event/Event";
import RedesUnity from "../soft/redesUnity/RedesUnity"
import './Media.css';
import mediaData from 'data/unit/Media.json';

class Media extends Component {

  render() {

    const { t, i18n } = this.props;

    return (
      <div className="Media">
        <UnityDescription unity={mediaData} />
        <p style={{ color: mediaData.colorTitles }} className="Media-title-services">{t('unit.services')}</p>
        <div className="Media-container-services">
          <img className="Media-background11-services" src={"https://drive.google.com/uc?id=1CFDkPjYQAMwEDcL3umUoB3A6UGTTuQnY"} alt={"background11"}></img>
          <div className="Media-container-background2-services">
            <img className="Media-background21-services" src={"https://drive.google.com/uc?id=1b5HHueW05mAJElLSVCLcoVKAdO75pcoF"} alt={"background21"}></img>
          </div>
          {
            mediaData.services.map(
              (service, index) => {
                return <div key={index} className="Media-container-service">
                <Service
                service={service}
                color={mediaData.color}
                colorTitle={mediaData.colorSubTitles}
                marcoServices={mediaData.marcoServices}
                />
                </div>
              }
            )
          }
        </div>
        {
          mediaData.technologies.map(
            (index) => {
              if(index === 0){
                return <p style={{ color: mediaData.colorTitles }} className="Media-title-services"><Trans i18nKey="unit.techs">technologies<br />used by us</Trans></p>
              }
            }
          )
        }
        <p style={{ color: mediaData.colorTitles }} className="Media-title-services"><Trans i18nKey="unit.techs">1<br />2</Trans></p>
        <div className="Media-container-technologies">
          {
            mediaData.technologies.map(
              (technology, index) => {
                return <Technology key={index} technology={technology} color={mediaData.color} />
              }
            )
          }
        </div>
        <p style={{ color: mediaData.colorTitles }} className="Media-title-services">{t('unit.portfolio')}</p>
         <div className="Media-container-portfolioCompleted">
          {
            mediaData.portfolio.completed.map(
              (portfolioCompleted, index) => {
                if(index === 0){
                return  <>
                        <div className="Media-container-background1-portfolioCompleted">
                          <img className="Media-background1-portfolioCompleted" src={"https://drive.google.com/uc?id=1t7KwSZdS2KizI9bVon-RL_lvwV5xPJ39"} alt={"backPortafolio1"}></img>
                        </div>
                        <img className="Media-background2-portfolioCompleted" src={"https://drive.google.com/uc?id=124mL9lUu2d-DbJTfvponggcOD12qDHc0"} alt={"backPortafolio2"}></img>
                        <p style={{ color: mediaData.colorTitles }} className="Science-completed-title">{t('unit.completed')}</p>
                        <PortfolioCompleted
                        key={index}
                        index={index}
                        portfolioCompleted={portfolioCompleted}
                        color={mediaData.color}
                        colorTitle={mediaData.colorSubTitles}
                        marcoServices={mediaData.marcoServices}
                        />
                        </>
                }
                else{
                    return <PortfolioCompleted
                    key={index}
                    index={index}
                    portfolioCompleted={portfolioCompleted}
                    color={mediaData.color}
                    colorTitle={mediaData.colorSubTitles}
                    marcoServices={mediaData.marcoServices}
                    link={mediaData.link}
                    />
                }
              }
            )
          }
        </div>
        <div className="Media-container-portfolioOngoing">
          {
            mediaData.portfolio.ongoing.map(
              (portfolioOngoing, index) => {
                return <PortfolioMedia
                key={index}
                index={index}
                portfolioOngoing={portfolioOngoing}
                color={mediaData.color}
                colorTitle={mediaData.colorSubTitles}
                />
              }
            )
          }
        </div>
        <p style={{ color: mediaData.colorTitles }} className="Media-title-services">{t('unit.events')}</p>
        <div className="Media-container-events">
          {
            mediaData.events.map(
              (event, index) => {
                return <Event
                key={index}
                event={event}
                color={mediaData.color}
                marcoServices={mediaData.marcoServices}
                />
              }
            )
          }
        </div>
        <RedesUnity redes={mediaData.redes}/>
      </div>
    );
  }
}

export default withTranslation()(Media);
